import './App.css';
import React, { useState } from 'react';

const App = () => {

  const allStates = ['Alabama', 'Alaska', 'Arizona', 'Arkansas', 'California', 'Colorado'];

  const [unseen, setUnseen] = useState(allStates);
  const [seen, setSeen] = useState([]);

  const seeState = (state) => {
    setUnseen(unseen.filter(p => p !== state))
    if (!seen.includes(state)) {
      setSeen([state].concat(seen).sort())
    }
  }

  const renderState = (state,seen) => {
    if (seen) {
      return (
        <div class="seen-state">{state}</div>
      )
    } else {
      return (
        <div class="unseen-state" onClick={() => seeState(state)}>{state}</div>
      )
    }
  }

  return (
    <div className="App">
      <header className="App-header">
        <div id="unseen">
          <p class="state-header">Unseen</p>
          {unseen.map(s => renderState(s,false))}
        </div>
        <div id="seen">
          <p class="state-header">Seen</p>
          {seen.map(s => renderState(s,true))}
        </div>
      </header>
    </div>
  );
}

export default App;
